import HeroBanner from "../../../components/HeroBanner";
import LeaderBoard from "../../../components/LeaderBoard";
import BrandSlide from "./BrandSlide";
import NewArrivals from "./NewArrivals";
import UtsavGallery from "./UtsavGallery";
import UtsavProducts from "./UtsavProducts";

const UtsavHome = () => {
  return (
    <main className="space-y-8 py-8">
      <HeroBanner position="Top" resourceType="Utsav" />
      <LeaderBoard position="Middle_1" resourceType="Utsav" />
      <UtsavGallery />
      <NewArrivals />
      <LeaderBoard position="Middle_2" resourceType="Utsav" />
      <BrandSlide />
      <UtsavProducts />
    </main>
  );
};

export default UtsavHome;
