import { useState, useEffect, useCallback } from "react";
import { getAllGalleryBanners } from "../api/bannerApis";

const useGallery = () => {
  const [banners, setBanners] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const fetchAllBanners = useCallback(async () => {
    setLoading(true);
    setError(null);
    try {
      const cacheKey = "galleryImages";
      const cachedData = sessionStorage.getItem(cacheKey);
      if (cachedData) {
        setBanners(JSON.parse(cachedData));
      } else {
        const response = await getAllGalleryBanners();

        if (response?.status) {
          console.log(response.data);
          sessionStorage.setItem(
            cacheKey,
            JSON.stringify(response.data.galleryDetails)
          );
          setBanners(response.data.galleryDetails);
        }
      }
    } catch (error) {
      console.error(error);
      setError(error.message);
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchAllBanners();
  }, [fetchAllBanners]);

  return { banners, loading, error };
};

export default useGallery;
