import { Link } from "react-router-dom";
import Gallery from "../../../components/Gallery";
import useGallery from "../../../hooks/useGallery";

const UtsavGallery = () => {
  const { banners } = useGallery();

  const renderSlides = (banners) => {
    return banners.map((banner) => (
      <Link
        to={`/category/${banner?.categoryId?._id}`}
        className="block relative"
      >
        <img
          src={banner.bannerImg}
          alt={banner.title || banner._id}
          loading="lazy"
        />
      </Link>
    ));
  };

  return (
    banners.length > 0 && (
      <div className="container max-w-full">
        <Gallery>{renderSlides(banners)}</Gallery>
      </div>
    )
  );
};

export default UtsavGallery;
